

* {
  box-sizing: border-box;
}


body, html{
    margin: 0;
    padding: 0;
    font-family: Arial, Helvetica, sans-serif;
    background:yellow;
    position: relative;
}

.app-container {
  height:100dvh;
  width:100vw;
  display:flex;
  justify-content:center;
  padding:0;
  background:blue;
}

.shapesIcon {
    position: absolute;
    top: -52px;
    left: -54px;
    width: 235px;
    height: 173px;
  }
  
  .shapesIcon1 {
    position: absolute;
    height: 20.5%;
    width: 60.26%;
    top: 84.24%;
    right: -18.97%;
    bottom: -4.74%;
    left: 58.72%;
  }

.page-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    height: fit-content;
    justify-content:center;
    position:relative;
    padding:0;
    background:red;
  }

:root{
    --pr-color:#32cd32;
    --sc-color:rgb(80, 128, 96);
}

/* Styles for screens larger than laptops (1024px and up) */
@media (min-width: 1024px) {
  .page-container {
    width: 512px;
    border:1px solid black;
    position:relative;
  }
}
